function render(vnode, container) {
    container.innerHTML= ''
    console.log(vnode)
    _render(vnode, container)
}

function _render (vnode, container) {
    let dom = createDomFromVnode(vnode)
    container.appendChild(dom)
}
function createDomFromVnode(vnode){
    // renderÁöÑ‰∏ªË¶ÅÂäüËÉΩÂ∞±ÊòØÊääËôöÊãüdomËΩ¨Êç¢‰∏∫ÁúüÂÆûdom
    // htmlÁöÑÂÖ≥ÈîÆÔºåÊó†ÈùûÂ∞±ÊòØÔºåtag,Â≠êÂÖÉÁ¥†ÂíåtagÈáåÁöÑÂÜÖÂÆπ

    // ËøôÈáåÂàõÂª∫tagÈáåÁöÑÂÜÖÂÆπ
    if(typeof vnode ==='string' || typeof vnode === 'number') {
        return document.createTextNode(vnode)
    }

    // ËøôÈáåÈÄíÂΩíÂàõÂª∫tagÂíå
    if(typeof vnode ==='object') {
        if(typeof vnode.tag === 'function') {
            let dom = createComponent(vnode.tag, vnode.attrs)
            return dom
        }
        let dom = document.createElement(vnode.tag)
        console.log('dom----->', dom)
        setAttribute(dom, vnode.attrs)
        if(vnode.children&&Array.isArray(vnode.children)) {
            vnode.children.forEach(vnodeChild=>{
                render(vnodeChild, dom)
            })
        }
        return dom
    }

}

function createComponent(constructor, attrs) {
    let component = new constructor(attrs)
    let vnode = component.render()
    let dom = createDomFromVnode(vnode)
    component.$root = dom
    return dom
}

function setAttribute(node, attrs) {
    for(let key in attrs) {
        // Â¶ÇÊûúÂºÄÂ§¥Êúâon,ËΩ¨Êç¢‰∏∫Â∞èÂÜô
        if(key.startsWith('on')) {
            node[key.toLocaleLowerCase()] = attrs[key]
        // styleÔºåË¶ÅÊµÖÊã∑Ë¥ù
        } else if(key === 'style') {
            Object.assign(node.style, attrs[key])
        // Ââ©‰∏ãÁöÑÂ±ûÊÄßÁõ¥Êé•Â§çÂà∂
        } else {
            node[key] = attrs[key]
        }
    }
}

export default {
    render
}